.belleza {
	@include pageComponent(rosita);

	&__ttl {
		color: c(titulo-single);
		font-family: f(sans);
		font-size: 46px;
		font-weight: bold;
		line-height: 55px;
		letter-spacing: 4px;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: s(1/4);
		text-align: center;
		text-transform: uppercase;
		max-width: 80%;
	}

	&__sbttl {
		color: #000000;
		font-size: 16px;
		font-family: f(serif);
		font-weight: 400;
		font-style: italic;
		text-align: center;
		margin-bottom: s(3/2);
	}

	.tags {
		text-align: center;
		.navegacion, .tag {
			display: inline-block;
			color: #888888;
			text-align: center;
			font-size: 11px;
			font-family: f(sans);
			font-weight: 200;
			margin-bottom: s(3/2);
		}
	}

	.p {
		position:relative;
		p {
			font-size: 16px;
			margin-left: auto;
			margin-right: auto;
			max-width: 750px;
			margin-bottom: s(5/4);
		}
		img {
			margin-bottom: s(3);
		}
	}

	.ano {
		$s: 12px;
		position: absolute;
		font-size: $s;
		top: 0;
		left: $s * -2;
	}

	.share {
		margin-bottom: s(4);
	}

	.mes {
		text-align: center;
		color: #000000;
		font-family: 'SharpSansNoOne-Bold';
		font-size: 23px;
		letter-spacing: 7px;
		text-transform: uppercase;
		text-decoration: underline;
		font-weight: normal !important;
		margin-bottom: s();
	}

	.divisor {
		 width: 100%;
		 max-width: 100%;
	}

	.anterior {
		@include anterioresComponent(rosita);
		&__main-container {
			height: auto;
			width: 285px;
		}
		.img-logo-container {
			margin-right: auto;
			margin-left: auto;
			height: 297px;
			width: 225px;
			overflow: hidden;
		}
		.imagen {
			height: auto;
			width: auto;
		}
		.well {
			height: 380px;
			padding: 0;
			padding-top: s();
		}

		&__ttl {
			a{
				height: auto;
				margin-right: auto;
				margin-left: auto;
				font-size: 18px;
				line-height: lh();
				max-width: calc(100% - #{s(1/2)});
			}
		}
	}
}
