.moda {
	@include pageComponent(crema);

	.container {
		@include under(sm) {
			width: calc(100% - 50px);
		}
	}

	.imagen.single {
		position:relative;
		right: -1*s();
		margin-bottom: 0;
		width: 100%;
		@include under(sm) {
			margin-bottom: s(1/2);
			right: 0;
		}
	}

	&__ttl {
		font-size: 58px;
		text-align: left;
		letter-spacing: -1px;
		margin-left: s();
		margin-bottom: s(3);
	}

	.anterior {
		@include anterioresComponent(crema);
	}

	.ano {
		@extend %vertical-text;

	}

	.creditos {
		@include under(sm) {
			margin-bottom: s(8/2);
		}

		p {
			@extend %paragraph;
			font-size: 13px;
			padding-left: s();
		}
	}

	.ver-mas {
		@extend %m0a;
		cursor: pointer;
		font-family: f(serif);
		font-size: 20px;
		letter-spacing: 1px;
		margin-top: s(2);
		margin-bottom: s(7/2);
		text-align: center;

	}

	.share__social {
		margin-bottom: s(7/2);
	}


	.linea-single {
		margin-left: s();
   		background: url('images/linea-single.jpg') no-repeat left top;
    	padding-top: 70px;
    	margin-top: 140px
	}
	.divisor {
		margin-bottom: s(3/2);
		max-width: 1141px;
		width: 100%;
	}

}
