@mixin articulo {
	margin-bottom: s(3);
	&-container{
		@include over(sm) {
			// margin-bottom: s(3);
		}
	}

	.ttl, .imagen { cursor: pointer;}

	.name, .ttl {
		font-family: f(sans);
		font-size: 14px;
		font-weight: 200;
		text-transform: uppercase;
		margin-bottom: s(5/4);
	}

	.ttl {
		// @extend %title;
		color: #333;
   		font-family: 'SharpSansNoOne-Bold';
   		text-transform: uppercase;
  		font-size: 15px;
    	letter-spacing: 2px;
		line-height: lh();
		margin-bottom: 0;
	}

	.imagen {
		height: 100%;
		cursor: pointer;
		margin-bottom: s(3/4);
		width: 100%;
	}

	.imagen-container {
		@include over(sm) {
			height: 375px;
		}
	}

	.categoria {
		@extend %title;
		font-size: 30px;
		margin-bottom: s(3/4);
	}

	.extracto {
		color: #333333;
    	font-family: f(serif);
    	font-size: 13px;
    	font-weight: normal;
    	line-height: 1.618;
	}
}