.footer {
	padding-bottom: s(3.5/2);	
	padding-left: s(5/2);
	padding-right: s(5/2);
	.--mb {
		margin-bottom: s();
	}
	.copyright, .social{
		@extend %paragraph;
		font-size: 12px;
	}
	.contacto {
		@extend %paragraph;
		font-size: 14px;
		font-weight: 600;
		text-decoration: underline;	
		text-transform: uppercase;
	}
	&__social {
		@extend %paragraph;
		font-size: 12px;
		display:inline-block;
	}
	.delimiter {
	    top: 2px;
	    position: relative;
	    font-size: 14px;	
	}
}