@mixin anterioresComponent($bgc) {
	$w:260px;
	// width: $w;
	@include under(sm) {
		margin-right: auto;
		margin-left: auto;
	}
	&__main-container {
		height: 450px;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: s(2);
		width: $w;
	}

	.fecha {
		@extend %vertical-text;
		font-size: 13px;
		margin-bottom: 7px;
		text-align: center;
		width: auto;
	}

	&__main-ttl, &__ver-por-fechas {
		font-family: f(sans);
		font-weight: 600;
		font-size: 14px;
		letter-spacing: 3px;
		text-transform: uppercase;
		text-align: center;
		margin-bottom: s(2);
	}

	&__ver-por-fechas {
		text-align: right;
		text-transform: none;
	}

	&__ttl {
		@extend %title;
		font-size: 32px;
		height: 76px;
		line-height: 38px;
		letter-spacing: 1px;
		text-align: center;
		margin-bottom: -14px;
	}

	.well {
		background-color: c($bgc);
		display: block;
		height: 361px;
		width: $w;
		padding: 30px;
	}

	.img-logo-container {
		position: relative;
	}

	.logo {
		@extend %vertical-text;
		position: absolute;
		font-size: 11px;
		line-height: 1;
		@include under(lg) {
			bottom: -34px;
		}
	}

	.imagen {
		height: 250px;
		width: 167px;
		margin-left: auto;
		margin-right: 0;
	}


	.descripcion {
		font-family: f(serif);
		font-size: 12px;
		font-weight: normal;
		text-align: right;
		position: relative;
		top: -6px;
	}
}
